<template>
    <v-container style="width: 100%; " align="center">
        <v-app-bar
                fixed
                app
                color="#FFFFFF"
                elevation="3"
        >
            <v-btn style="display: inline; padding-left: 0px !important;" text icon color="primary"
                   @click="$router.go(-1)">
                <v-icon>mdi-arrow-left</v-icon>
            </v-btn>
            <v-spacer></v-spacer>
            <v-toolbar-title>{{filtersTitle}}</v-toolbar-title>
            <v-spacer></v-spacer>
            <v-btn small style="display: inline; height: 100%;" text color="primary"
                   @click="resetFilters">{{removeFiltersText}}
            </v-btn>

        </v-app-bar>
        <div style="margin-bottom: 15px">
            <v-row style="margin-top: 54px !important" dense>
                <v-col cols="12">
                    <v-card class="text-center"
                    >
                        <v-card-title class="filter-cards-titles">{{distanceFilterTitle}}</v-card-title>
                        <v-col>
                            <div>
                                <v-slider
                                        v-model="filters.maxDistance"
                                        :tick-labels="filters.maxDistanceRangesText"
                                        :max="3"
                                        step="1"
                                ></v-slider>
                            </div>
                        </v-col>
                    </v-card>
                </v-col>
            </v-row>
            <v-row dense>
                <v-col cols="12">
                    <v-card class="text-center">
                        <v-card-title class="filter-cards-titles">{{genderFilterTitle}}</v-card-title>
                        <v-card-actions class="justify-center">
                            <div style="display: table; height: fit-content; margin-right: 12px">
                                <v-checkbox v-model="filters.genderM"
                                            style="display: table-cell; vertical-align: middle;"></v-checkbox>
                                <img :src="createPathToGenderImage(genders[0].img)"
                                     style="height: 24px; width: auto; display: table-cell; vertical-align: middle;"/>
                                <p style="display: table-cell; vertical-align: top;"> {{ genders[0].name }}</p>
                            </div>

                            <div style="display: table; height: fit-content; margin-left: 12px">
                                <v-checkbox v-model="filters.genderW"
                                            style="display: table-cell; vertical-align: middle;"></v-checkbox>
                                <img :src="createPathToGenderImage(genders[1].img)"
                                     style="height: 24px; width: auto; display: table-cell; vertical-align: middle;"/>
                                <p style="display: table-cell; vertical-align: top;"> {{ genders[1].name }}</p>
                            </div>
                        </v-card-actions>
                    </v-card>
                </v-col>
            </v-row>
            <v-row dense>
                <v-col cols="12">
                    <v-card class="text-center"
                    >
                        <v-card-title class="filter-cards-titles">{{ageFilterTitle}}</v-card-title>
                        <v-col>
                            <p align="center" class="primary--text"><strong>{{filters.ageRange[0]}} -
                                {{filters.ageRange[1]}} años</strong></p>
                            <div>
                                <v-range-slider
                                        v-model="filters.ageRange"
                                        :max="maxAge"
                                        :min="minAge"
                                        hide-details
                                        class="align-center"
                                >
                                </v-range-slider>
                            </div>
                        </v-col>
                    </v-card>
                </v-col>
            </v-row>

            <v-row dense>
                <v-col cols="12">
                    <v-card class="text-center"
                    >
                        <v-card-title class="filter-cards-titles">{{languageFilterTitle}}</v-card-title>
                        <v-card-actions class="justify-center">
                            <v-radio-group v-model="languagesRadioButtons" :mandatory="false"
                                           style="margin: 0px!important">
                                <div style="padding: 10px;">
                                    <v-radio class="languageRadioButton" :label="textFilterByLanguageToLearn"
                                             value="languageToLearnRadioButton">
                                        <template v-slot:label>
                                            <strong v-bind:class="{'primary--text': languagesRadioButtons == 'languageToLearnRadioButton'}">{{textFilterByLanguageToLearn}}</strong>
                                        </template>
                                    </v-radio>
                                    <v-select
                                            v-model="filters.languageToLearn"
                                            :disabled="languagesRadioButtons != 'languageToLearnRadioButton'"
                                            style="margin-top: 15px"
                                            :items="languages"
                                            item-value="id"
                                            item-text="option"
                                            dense
                                            return-object
                                    >

                                        <template v-slot:item="{item}">
                                            <v-avatar>
                                                <img :src="createPathToFlagImage(item.id)"
                                                     style="height: 60%; width: auto"/>
                                            </v-avatar>
                                            {{ item.text }}
                                        </template>
                                        <template v-slot:selection="{item, select, selected }">
                                            <v-avatar>
                                                <img :src="createPathToFlagImage(item.id)"
                                                     style="height: 50%; width: auto"/>
                                            </v-avatar>
                                            {{ item.text }}
                                        </template>
                                    </v-select>
                                </div>
                                <div style="padding: 10px">
                                    <v-radio class="languageRadioButton" :label="textFilterByKnownLanguage"
                                             value="knownLanguageRadioButton">
                                        <template v-slot:label>
                                            <strong v-bind:class="{'primary--text': languagesRadioButtons != 'languageToLearnRadioButton'}">{{textFilterByLanguageToLearn}}</strong>
                                        </template>
                                    </v-radio>
                                    <v-select
                                            v-model="filters.knownLanguage"
                                            :disabled="languagesRadioButtons == 'languageToLearnRadioButton'"
                                            style="margin-top: 15px"
                                            :items="languages"
                                            item-value="id"
                                            item-text="option"
                                            dense
                                            return-object
                                    >

                                        <template v-slot:item="{item}">
                                            <v-avatar>
                                                <img :src="createPathToFlagImage(item.id)"
                                                     style="height: 60%; width: auto"/>
                                            </v-avatar>
                                            {{ item.text }}
                                        </template>
                                        <template v-slot:selection="{item, select, selected }">
                                            <v-avatar>
                                                <img :src="createPathToFlagImage(item.id)"
                                                     style="height: 50%; width: auto"/>
                                            </v-avatar>
                                            {{ item.text }}
                                        </template>
                                    </v-select>
                                </div>
                            </v-radio-group>
                        </v-card-actions>
                    </v-card>
                </v-col>
            </v-row>
        </div>
        <v-btn tile large class="bottom-fixed-button" style="display: inline" color="primary" @click="filterListUsers">
            {{showUsersText}}
        </v-btn>
    </v-container>
</template>

<script>
    import Constants from "../../store/Constants";
    import {mapMutations} from "vuex";

    export default {
        name: "UserProfile",
        data() {
            return {
                genders: null,
                filters: null,
                languages: null,
                knownLanguageRadioButtonDisabled: null,
                languageToLearnRadioButtonDisabled: null,
                languagesRadioButtons: "languageToLearnRadioButton",
                slider: 40,
                minAge: Constants.MIN_USER_AGE,
                maxAge: Constants.MAX_USER_AGE,
                textFilterByLanguageToLearn: Constants.TEXT_FILTER_BY_LANGUAGE_TO_LEARN,
                languagesToLearnTitle: Constants.TEXT_LANGUAGE_HE_SHE_WANT_TO_PRACTICE,
                textFilterByKnownLanguage: Constants.TEXT_FILTER_BY_MOTHER_LANGUAGE,
                knownLanguagesTitle: Constants.TEXT_MOTHER_LANGUAGES,
                knownLanguageLabel: Constants.TEXT_LANGUAGE_HE_SHE_SPEAK_FLUENTLY,
                languageToLearnLabel: Constants.TEXT_LANGUAGE_HE_SHE_WANT_TO_IMPROVE,
                ageFilterTitle: Constants.TEXT_AGE,
                distanceFilterTitle: Constants.TEXT_DISTANCE,
                genderFilterTitle: Constants.TEXT_GENDER,
                languageFilterTitle: Constants.TEXT_LANGUAGE,
                filtersTitle: Constants.TEXT_FILTERS,
                removeFiltersText: Constants.TEXT_REMOVE_FILTERS,
                toFilterText: Constants.TEXT_TO_FILTER,
                showUsersText: Constants.TEXT_SHOW_USERS
            }
        },
        methods: {
            ...mapMutations([
                'setSearchInfoFiltering',
                'resetSearchInfoFiltering',
                'resetSearchInfoUserIDs'
            ]),
            filterListUsers() {
                this.setSearchInfoFiltering(this.filters);
                this.resetSearchInfoUserIDs();
                this.$router.replace({name: 'searchHome'})
            },
            resetFilters() {
                this.resetSearchInfoFiltering();
                this.getFiltersInfo();
            },
            createPathToFlagImage: function (lang) {
                return "../img/flags/" + lang + ".svg";
            },
            createPathToGenderImage: function (path) {
                return "../" + path;
            },
            getFiltersInfo(){
                this.genders = this.$store.getters.getAllAvailableGendersAsArray;
                this.languages = this.$store.getters.getAllAvailableLanguagesAsArray;
                this.languageToLearnRadioButtonDisabled = !this.$store.getters.getSearchInfoFilteringFilterByLanguageToLearn;
                this.knownLanguageRadioButtonDisabled = this.$store.getters.getSearchInfoFilteringFilterByLanguageToLearn;
                this.filters = this.$store.getters.getSearchInfoFiltering;
            }
        },
        computed: {},
        beforeMount() {
            this.getFiltersInfo();
        }
    }
</script>

<style scoped>

    .bottom-fixed-button {
        position: fixed;
        bottom: 57px;
        left: 0px;
        width: 100%;
    }
</style>
